import React from 'react';
import ReactDOM from 'react-dom';
import { useState, useEffect, FormEvent } from 'react';

import './App.css';
import * as C from './App.styles';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Carousel, Container  } from 'react-bootstrap';

import logo from './assets/images/PokeLogo.png';
import loading from './assets/images/PokeLoading.gif';
import calendar from './assets/images/Calendar-icon.png';
import contacts from './assets/images/Contacts-icon.png';
import folder from './assets/images/Folder-Music-icon.png';
import health from './assets/images/Health-icon.png';
import reminder from './assets/images/Reminders-icon.png';
import iBook from './assets/images/iBook-icon.png';
import iTunes from './assets/images/iTunes-Store-icon.png';
import photos from './assets/images/Photos-icon.png';
import safari from './assets/images/Safari-icon.png';
import stocks from './assets/images/Stocks-icon.png';
import weather from './assets/images/Weather-icon.png';
import game from './assets/images/Game-Center-icon.png';
import appStore from './assets/images/App-Store-icon.png';
import settings from './assets/images/Settings-icon.png';
import camera from './assets/images/Camera-icon.png';
import calculator from './assets/images/Calculator-icon.png';
import message from './assets/images/Messages-icon.png';
import phone from './assets/images/Phone-icon.png';
import clock from './assets/images/Clock-icon.png';
import appPokemon from './assets/images/pokemon-icon.png';

import * as Poke from './services/PokeJSON';
import { Pokemon } from './types/Pokemon';
import { PokeItem } from './components/PokeItem';
import { PokeFloat } from './components/FloatWindown';


 
function App() {
  const [appLoading, setAppLoading] = useState(true);
  const [mobileView, setMobileView] = useState(false);
  const [appMobile, setAppMobile] = useState(false);
  const [pokemon, setPokemon] = useState<Pokemon[]>([]);
  const [pokeStats, setPokeStats] = useState<Pokemon[]>([]);
  const [float, setFloat] = useState(false);

  useEffect(()=>{
    const getPokemons = async () => {
      setAppLoading(true);
      setPokemon(await Poke.getAllPokemons());
      setAppLoading(false);
    } 
    getPokemons()
  }, []); 


  const setPokemonFloat = async (event: any) => {
    setPokeStats(await Poke.getPokemonByIndex(pokemon[event]));
    setFloat(true);
  }

  const handleSelect = (eventKey: any, e: any) => {
    let classe = e.target.className;

    if(classe == 'carousel-control-next' || classe == 'carousel-control-next-icon') {
      const getNextPokeFloat = async () => {
        setPokeStats(await Poke.getPokemonByIndex(pokemon[pokeStats[0].id]));
        setFloat(true);
        console.log(pokeStats[0].id);
      } 
      getNextPokeFloat(); 
      
    }
    
    if(classe == 'carousel-control-prev' || classe == 'carousel-control-prev-icon') {
      const getPrevPokeFloat = async () => {
        setPokeStats(await Poke.getPokemonByIndex(pokemon[pokeStats[0].id - 2]));
        setFloat(true);
        console.log(pokeStats[0].id);
      } 
      getPrevPokeFloat(); 
    }
  };

  const mobileFunction = () => {
    if (mobileView) {
      setMobileView(false);
    } else {
      setMobileView(true);
    }
  }


  return (
    <C.PokeApp>
      <C.Header>
        <Container>
          <img src={logo} alt="Imagem não encontrada" />
          <span onClick={mobileFunction}>
            {mobileView &&
              'Desktop'
            }
            {!mobileView &&
              'Mobile'
            }
          </span>
        </Container>
      </C.Header>

      {mobileView &&
        <C.MobileMode>
          {!appMobile &&
            <Container>
              <div className="iphone-screen initial">
                <div className="content">
                  <div className="item">
                    <img src={calendar} alt="" />
                    <span>Calendário</span>
                  </div>
                  <div className="item">
                    <img src={contacts} alt="" />
                    <span>Contatos</span>
                  </div>
                  <div className="item">
                    <img src={folder} alt="" />
                    <span>Arquivos</span>
                  </div>
                  <div className="item">
                    <img src={health} alt="" />
                    <span>Saúde</span>
                  </div>
                  <div className="item">
                    <img src={reminder} alt="" />
                    <span>Notas</span>
                  </div>
                  <div className="item">
                    <img src={iBook} alt="" />
                    <span>iBook</span>
                  </div>
                  <div className="item">
                    <img src={iTunes} alt="" />
                    <span>iTunes</span>
                  </div>
                  <div className="item">
                    <img src={photos} alt="" />
                    <span>Fotos</span>
                  </div>
                  <div className="item">
                    <img src={safari} alt="" />
                    <span>Safari</span>
                  </div>
                  <div className="item">
                    <img src={stocks} alt="" />
                    <span>Stocks</span>
                  </div>
                  <div className="item">
                    <img src={weather} alt="" />
                    <span>Clima</span>
                  </div>
                  <div className="item">
                    <img src={game} alt="" />
                    <span>Game Center</span>
                  </div>
                  <div className="item">
                    <img src={appStore} alt="" />
                    <span>App Store</span>
                  </div>
                  <div className="item">
                    <img src={calculator} alt="" />
                    <span>Calculadora</span>
                  </div>
                  <div className="item">
                    <img src={settings} alt="" />
                    <span>Configurações</span>
                  </div>
                  <div className="item">
                    <img src={appPokemon} alt="" />
                    <span>PokeApp</span>
                  </div>
                  <div className="item">
                    <img src={clock} alt="" />
                    <span>Relógio</span>
                  </div>
                </div>
                <div className="side-bar">
                  <div className="item">
                    <img src={phone} alt="" />
                    <span>Telefone</span>
                  </div>
                  <div className="item">
                    <img src={camera} alt="" />
                    <span>Câmera</span>
                  </div>
                  <div className="item">
                    <img src={message} alt="" />
                    <span>Mensagens</span>
                  </div>
                </div>
              </div>
              <div className="model"></div>
            </Container>              
          }
          {appMobile &&
            <Container>
              <div className="iphone-screen mobile">
                {!appLoading && float &&         
                  <C.FloatMobile>
  {/*                     <Carousel variant="dark" indicators={false} interval={null} onSelect={handleSelect}>
                      <Carousel.Item>              
                        {pokeStats.map((item, index)=>(
                          <PokeFloat
                            key={index} 
                            image={item.image} 
                            id={item.id} 
                            name={item.name} 
                            types={item.types} 
                            abilities={item.abilities}
                            stats={item.stats}
                            height={item.height}
                            weight={item.weight}
                            description={item.description}
                            onClick={() => {setFloat(false)}}
                          />
                        ))}
                      </Carousel.Item>
                    </Carousel> */}
                  </C.FloatMobile>
                }
                {!appLoading && pokemon.length > 0 &&
                  <C.Mobile>
                    <Container>
                      <C.PokeList className="getAllPokemons"> 
                        {pokemon.map((item, index)=>(
                          <PokeItem
                            key={index}
                            image={item.image} 
                            id={item.id} 
                            name={item.name} 
                            types={item.types} 
                            abilities={item.abilities}
                            stats={item.stats}
                            height={item.height}
                            weight={item.weight}
                            description={item.description}
                            onClick={setPokemonFloat}
                          />                
                        ))}
                      </C.PokeList>
                    </Container>
                  </C.Mobile>          
                } 
              </div>
              <div className="model-b"></div>
            </Container>
          }            
        </C.MobileMode>
      }

      {!mobileView &&
        <C.Screen>
        {appLoading &&
          <C.LoadingApp>
            <img src={loading} alt="" />
            Aguarde, carregando...
          </C.LoadingApp>
        }


        {!appLoading && float &&         
          <C.FloatWindow>
            <Carousel variant="dark" indicators={false} interval={null} onSelect={handleSelect}>
              <Carousel.Item>              
                {pokeStats.map((item, index)=>(
                  <PokeFloat
                    key={index} 
                    image={item.image} 
                    id={item.id} 
                    name={item.name} 
                    types={item.types} 
                    abilities={item.abilities}
                    stats={item.stats}
                    height={item.height}
                    weight={item.weight}
                    description={item.description}
                    onClick={() => {setFloat(false)}}
                  />
                ))}
              </Carousel.Item>
            </Carousel>
          </C.FloatWindow>
        }

        {!appLoading && pokemon.length > 0 &&
        <C.Main>
          <Container>
            <C.PokeList className="getAllPokemons"> 
              {pokemon.map((item, index)=>(
                <PokeItem
                  key={index}
                  image={item.image} 
                  id={item.id} 
                  name={item.name} 
                  types={item.types} 
                  abilities={item.abilities}
                  stats={item.stats}
                  height={item.height}
                  weight={item.weight}
                  description={item.description}
                  onClick={setPokemonFloat}
                />                
              ))}
            </C.PokeList>
          </Container>
        </C.Main>          
        }   
        </C.Screen>      
      }
      
    </C.PokeApp>
  );
}

export default App;
