import styled from "styled-components";

export const Container = styled.div`

    background-color: #f2f2f2;
    border-radius: 0.5rem;
    padding: 1rem 0;
    display: flex;
    max-height: 20rem;

    img {
        max-heith: 100%;
        max-width: 50%;
        display: flex;
        border-radius: 0.5rem;
        align-items: center;
        justify-content: center;
        padding: 1rem;   
    }

    .pokeDetails {
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        padding: 0 1rem;
        border-left: 1px solid #CCC;

        .board {
            padding: 0.5rem;
            background-color: #e7e7e7;
            border-radius: 0.5rem;
            text-align: center;
        }   

        .details-top {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            .board.number {
                color: gray;
                font-weight: bold;
                font-size: 1rem;
                display: flex;
                justify-content: space-between;
            }
            .board.types {
                display: flex;
                gap: 0.5rem;
    
                .type-item {
                    flex: 1;
                    text-align: center;
                    font-size: 1rem;
                    padding: 0.3rem;
                    border-radius: 0.5rem;
                    text-transform: uppercase;
                    font-weight: bold;
                }
            }
        }

        .details-bottom {
            display: flex;

            .board.view {
                background-color: #30a7d7;
                color: #f9f9f9;
                flex: 1;
                font-size: 1.2rem;
                cursor: pointer;
            }
        }



        b {
            text-transform: capitalize;
            font-size: 1.8rem;
        }

    }

    &:hover {
        box-shadow: 0 0 0.5rem #a1a1a1;
    }
`;
