import styled from "styled-components";

export const Container = styled.div`
    display: flex;
    flex-direction: column;
    height: 50rem;
    width: 80rem;
    padding: 2rem;
    background-color: #f9f9f9;
    box-shadow: 0.3rem 0.3rem 1.3rem #5c5c5c;
    gap: 20px;
    border-radius: 0.5rem;



    .top-section {
        flex: 3;
        display: flex;
        gap: inherit;

        .left-side {
            flex: 1;
            max-width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;


            img {
                max-width: 100%;
            }
        }

        .right-side {
            flex: 1;
            max-width: 50%;
            display: flex;
            flex-direction: column;

            .poke-float-details {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                padding-left: 1rem;
                border-left: 1px solid #CCC;
        
                .board {
                    padding: 0.5rem;
                    background-color: #e7e7e7;
                    border-radius: 0.5rem;
                }   
        
                .details-top {
                    display: flex;
                    flex-direction: column;
                    gap: 0.5rem;
        
                    .board.number {
                        color: gray;
                        font-weight: bold;
                        font-size: 1rem;
                        display: flex;
                        justify-content: space-between;
                    }
                    .board.types {
                        display: flex;
                        gap: 0.5rem;
            
                        .type-item {
                            flex: 1;
                            text-align: center;
                            font-size: 1rem;
                            padding: 0.3rem;
                            border-radius: 0.5rem;
                            text-transform: uppercase;
                            font-weight: bold;
                        }
                    }
                    .board.h-and-w {
                        display: flex;
                        gap: 0.5rem;

                        span {
                            flex: 1;
                            font-size: 1.4rem;
                            padding: 0.3rem;
                            border-radius: 0.5rem;
                            text-transform: capitalize;

                            b {
                                font-size: 1.4rem;
                            }
                        }
                    }
                }
        
                .stats {
                    display: grid;
                    grid-template-columns: repeat(6, 5rem);
                    gap: 1.2rem;
                    padding-top: 2rem;
                    height: 100%;
                    width: 100%;
                    
                    .s-item {
                        display: flex;
                        flex-direction: column;

                        .s-item-box {
                            flex: 1;
                            display: flex;
                            align-items: flex-end;
                            padding: 0.5rem;
                            border: 1px solid #CCC;
                            border-radius: 0.5rem;                           
                            

                            .s-item-box-bar {
                                transition: all linear 500ms;
                                background-color: var(--stats);
                                width: 100%;
                                height: 1%;
                                max-height: 100%;                                
                            }
                        }
                        .s-item-name {
                            font-size: 1rem;
                            font-weight: bold;
                            text-align: center;
                        }
                    }
                }
        
                b {
                    text-transform: capitalize;
                    font-size: 1.8rem;
                }
        
            }
        
        }

    }

    .bottom-section {
        padding: 1rem;
        heigth: fit-content;
        display: flex;
        justify-content: center;
        
        .close {
            cursor: pointer;
        }
    }  
`;
