import styled from 'styled-components';
import iosBackground from './assets/images/background-screen.jpg'
import whiteBackground from './assets/images/white-screen.jpg'
import model from './assets/images/model.png'

export const PokeApp = styled.div`
    display: flex;
    flex-direction: column;
    max-height: 100vh;
    overflow: viseble;
`;

export const Content = styled.section`
    flex: 1;
    padding: 2rem 0;
`;

export const Header = styled.header`
    height: var(--headerHeight);
    background-color: var(--redColor);
    display: flex;
    align-items: center;
    padding: 1rem;
    box-shadow: 0 0 0.5rem #090909;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
            height: var(--logoHeight);
        }

        span {
            font-weight: bold;
            color: #f9f9f9;
            background-color: rgb(0 0 0 / 40%);
            font-size: 1.6rem;
            cursor: pointer;
            border-radius: 0.5rem;
            padding: 0.5rem;
        }
    }

`;

export const MobileMode = styled.div`
    height: calc(100vh - var(--headerHeight));

    .container {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 3rem;

        .iphone-screen {
            height: 75vh;
            width: calc(82.6vh / 2.16);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            padding: 55px 28px 30px 35px;
    
            .content {                
                height: fit-content;
                width: 100%;  
                display: grid;
                grid-template-columns: repeat(4, 1fr);
                gap: 1rem;
                align-self: flex-start;

                .item {
                    display: flex;
                    height: fit-content;
                    flex-direction: column;
                    align-items: center;
                    flex: 1;  
                    
                    img {
                        height: 55px;    
                    }

                    span {
                        color: #f9f9f9;
                    }

                    &:hover {
                        cursor: pointer;
                    }                    
                }
            }

            .side-bar {
                width: 100%;  
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                align-items: center;
                gap: 1rem;
                background-color: rgb(56 56 56 / 40%);;
                border-radius: 3rem;
                padding: 1.5rem;
                
                .item {
                    display: flex;
                    height: fit-content;
                    flex-direction: column;
                    align-items: center;
                   
                    img {
                        height: 55px;
                    }

                    span {
                        color: #f9f9f9;
                    }

                    &:hover {
                        cursor: pointer;
                    }                    
                }
            }
            }
        }
        .iphone-screen.initial {
            background: url('${model}'); 
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            position: absolute; 
        }
        .iphone-screen.mobile {
            background: url('${model}'); 
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            position: absolute;
        }
        .model {
            height: 73vh;
            width: calc(77.6vh / 2.16);
            background: url('${iosBackground}'); 
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            border-radius: 50px;
        }
        .model-b {
            height: 73vh;
            width: calc(77.6vh / 2.16);
        }
        
    }
`;

export const Screen = styled.div`
    height: calc(100vh - var(--headerHeight));
    
    .container {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        padding: 0;

        .getAllPokemons::-webkit-scrollbar {
            width: 0.3rem;
        }
        .getAllPokemons::-webkit-scrollbar-track {
            background-color: #f9f9f9;
        }
        .getAllPokemons::-webkit-scrollbar-thumb {
            background-color: #555;
        }
    }

`;

export const Main = styled.div`
    height: calc(100vh - var(--headerHeight));
    padding: 3rem;
    widht: 100vw;

    .container {        
        border-top: 0.3rem solid #CCC;
        border-bottom: 0.3rem solid #CCC;
    }
`;

export const Mobile = styled.div`
    height: 100%;
    widht: 100vw;
    padding: 3rem 0;

    .container {
        padding: 0;
        border: none;
 
        .getAllPokemons {
            grid-template-columns: repeat(1, 1fr);
            padding: 0 0.5rem;
        }
        .getAllPokemons::-webkit-scrollbar {
            width: 0.3rem;
        }
        .getAllPokemons::-webkit-scrollbar-track {
            background-color: #f9f9f9;
        }
        .getAllPokemons::-webkit-scrollbar-thumb {
            background-color: #555;
        }
    }
`;

export const LoadingApp = styled.div`
    height: calc(100vh - var(--headerHeight));
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f3fcff;
    position: absolute;

    img {
        height: 10rem;
        widht: 10rem;
    }
`;

export const PokeList = styled.div`
    @media screen and (max-width: 800px) {  
        grid-template-columns: repeat(2, 1fr);
    }

    @media screen and (min-width: 800px) and (max-width: 1024px) {  
        grid-template-columns: repeat(3, 1fr);
    }

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    overflow:  auto;
    height: 100%;
    max-height: 100%;
    padding: 2rem;
    padding-right: 0.9rem;
    padding-top: 0;


`;


export const FloatWindow = styled.div`
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - var(--headerHeight));
    width: 100vw;
    background-color: rgb(56 56 56 / 70%);
`;

export const FloatMobile = styled.div`
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    background-color: rgb(56 56 56 / 70%);
`;

